import { createSlice } from '@reduxjs/toolkit';
import { showAllCountries, searchByCode, searchByRegion } from './countriesAction';

const initialState = {
    loading: false,
    countriesData: [],
    countryData: [],
    error: false,
    success: false,
    message: "",
    region:"",
    searchCountry:"",
}

export const countriesSlice = createSlice({
    name: "countries",
    initialState: initialState,
    reducers: {
        reset: (state) => {
            state.loading = false;
            state.success = false;
            state.error = false;
            state.message = "";
            state.countryData = [];
            state.region="";
        },
        setRegion:(state,action)=>{
            state.region = action.payload;
        },
        setSearchCountry:(state,action)=>{
            state.searchCountry = action.payload
        }
    },
    extraReducers: (builder) => {
        builder
            .addCase(showAllCountries.pending, (state) => {
                state.loading = true;
            })
            .addCase(showAllCountries.fulfilled, (state, action) => {
                state.loading = false;
                state.countriesData = action.payload;
                state.success = true;
            })
            .addCase(showAllCountries.rejected, (state, action) => {
                state.loading = false;
                state.error = true;
                state.message = action.payload;
                state.countriesData = [];
            })

            //search by cios
            .addCase(searchByCode.pending, (state) => {
                state.loading = true;
            })
            .addCase(searchByCode.fulfilled, (state, action) => {
                state.loading = false;
                state.countryData = action.payload;
                state.success = true;
            })
            .addCase(searchByCode.rejected, (state, action) => {
                state.loading = false;
                state.error = true;
                state.message = action.payload;
                state.countryData = [];
            })

            //serach by region
            .addCase(searchByRegion.pending, (state) => {
                state.loading = true;
            })
            .addCase(searchByRegion.fulfilled, (state, action) => {
                state.loading = false;
                state.countriesData = action.payload;
                state.success = true;
            })
            .addCase(searchByRegion.rejected, (state, action) => {
                state.loading = false;
                state.error = true;
                state.message = action.payload;
                state.countryData = [];
            })
    }
})

export const { reset,setRegion,setSearchCountry} = countriesSlice.actions;

export default countriesSlice.reducer