import React, { useEffect } from 'react'
import "./country.css";
import { useSelector, useDispatch } from 'react-redux'
import { searchByRegion, showAllCountries } from "../../features/countries/countriesAction";
import { Link } from 'react-router-dom'

const Country = () => {

  const { countriesData, loading, message, success, error, region, searchCountry } = useSelector((state) => state.country)
  const dispatch = useDispatch();

  // const [countryData, setCountryData] = useState([]);

  useEffect(() => {
    dispatch(showAllCountries());

    // if (success) {
    //   setCountryData(countriesData)
    // }
    if (region) {
      dispatch(searchByRegion(region))
    }
    if (error) {
      console.log(error)
    }
  }, [dispatch, error, region, success, searchCountry])
  const data = countriesData.filter((item) => item.name.common.toLowerCase().includes(searchCountry) && item.cioc !== undefined);

  if (data.length === 0 && searchCountry.length !== 0) {
    return (
      <div style={{ justifyContent: 'center', display: 'flex' }}>
        <h1>No Found</h1>
      </div>
    )
  }

  const showNoFoundMessage = searchCountry.length > 0 && data.length === 0;

  return (
    <section className="country-container">
      {error ? (
        <h1>Error {message}</h1>
      ) : (
        <>
          {loading ? (
            <h1> Loading..</h1>
          ) : (
            <>
              {showNoFoundMessage && <h1>No Found</h1>}
              {!showNoFoundMessage &&
                data.length > 0 && data.map((item, index) => {
                  return (
                    <Link className="country-card" key={index} to={`/${item.cioc}`}>
                      <img src={item.flags.png} alt={item.flags.alt} className="country-image" />
                      <div className="country-content">
                        <h3>{item.name.common}</h3>
                        <p>
                          Population: <span>{item.population}</span>
                        </p>
                        <p>
                          Region: <span>{item.region}</span>
                        </p>
                        <p>
                          Capital: <span>{item.capital}</span>
                        </p>
                      </div>
                    </Link>
                  )
                })
              }
            </>
          )
          }
        </>


      )}


    </section >
  );
};

export default Country