import "./country-detail.css";
import { useSelector, useDispatch } from 'react-redux';
import { searchByCode } from "../../features/countries/countriesAction";
import { reset, setSearchCountry } from "../../features/countries/countriesSlice";
import { Link, useParams } from 'react-router-dom'
import { useEffect, useState } from "react";

const CountryDetail = () => {

  const { countryData, loading, success, error, message } = useSelector((state) => state.country)

  const dispatch = useDispatch();
  const { code } = useParams();
  const [showNotFound,setShowNotFound] = useState(false);

  useEffect(() => {
    setShowNotFound(false)

    if (code) {
      dispatch(searchByCode(code.toLowerCase()))
    }
    if (error) {
      console.log(error)
    }
    return () => {
      dispatch(reset());
    }

  }, [dispatch, error, code])

  useEffect(()=>{
    if(!countryData.length && !loading){
      setShowNotFound(true)
    }
  },[countryData,loading])
 
  useEffect(() => {
    return () => {
      dispatch(setSearchCountry('')); 
    };
  }, [dispatch]);

  if (loading) {
    return (
      <div style={{ display: 'flex', justifyContent: 'center', alignItems: 'center', height: '100vh' }}>
        <h1>Loading....</h1>
      </div>
    );
  }

  return (
    <section className="country-detail-container">
      <Link className="back-button" to="/">
        <i className="fa-solid fa-arrow-left"></i> Back
      </Link>

      <div className="country-detail-content">
        {countryData.length > 0 ? (
          <>
            <img src={countryData[0].flags.png} alt="name" className="country-detail-image" />

            <div className="country-detail-right">
              <h1>{countryData[0].name.common}</h1>
              <div className="details">
                <div className="detail-left">
                  <p>
                    Offcial Name: <span>{countryData[0].name.official}</span>
                  </p>
                  <p>
                    Population: <span>{countryData[0].population}</span>
                  </p>
                  <p>
                    Region: <span>{countryData[0].region}</span>
                  </p>

                  <p>
                    Sub Region: <span>{countryData[0].subregion}</span>
                  </p>
                  <p>
                    Capital: <span>{countryData[0].capital}</span>
                  </p>
                </div>

                <div className="detail-right">
                  <p>
                    Top Level Domain: <span>{countryData[0].tld[0]}</span>
                  </p>
                  <p>Currencies :
                    <span>
                      {Object.values(countryData[0].currencies).map((item) => {
                        return item.name;
                      }).join(",")}
                    </span>
                  </p>

                  <p>Languages :
                    <span>
                      {Object.values(countryData[0].languages).map((item) => {
                        return item
                      }).join(",")}
                    </span>
                  </p>
                </div>
              </div>

              <div className="border">
                <p>Border Countries:</p>
                {countryData[0].borders ? (
                  countryData[0].borders.map((item, index) => {
                    return (
                      <Link className="border-name" to={`/${item}`} key={index}><p>{item}</p></Link>
                    )
                  })
                ) : (<span>No Borders</span>)}
              </div>
            </div>
          </>
        ) : (<h1>No detail Found</h1>)}

      </div>


    </section>
  );
};

export default CountryDetail;
